@import url(//fonts.googleapis.com/css?family=Montserrat:300,400,500);
/* Below animations are fore modal created using React-Modal */
.ReactModal__Overlay {
    transition: -webkit-transform 300ms ease-in-out;
    transition: transform 300ms ease-in-out;
    transition: transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
    transition-delay: 100ms;
    -webkit-transform: scale(0);
            transform: scale(0);
    /* transform: translateY(-150%); */
}

.ReactModal__Overlay--after-open{
    -webkit-transform: scale(1);
            transform: scale(1);
    /* transform: translateY(0%); */
}

.ReactModal__Overlay--before-close{
    -webkit-transform: scale(0);
            transform: scale(0);
    /* transform: translateY(-150%); */
}

.contact2{font-family:"Montserrat", sans-serif;color:#8d97ad;font-weight:300;padding:60px 0;margin-bottom:170px;background-position:center top}.contact2 h1,.contact2 h2,.contact2 h3,.contact2 h4,.contact2 h5,.contact2 h6{color:#3e4555}.contact2 .font-weight-medium{font-weight:500}.contact2 .subtitle{color:#8d97ad;line-height:24px}.contact2 .bg-image{background-size:cover;position:relative;display:-webkit-flex;display:flex}.contact2 .card.card-shadow{box-shadow:0px 0px 30px rgba(115,128,157,0.1)}.contact2 .detail-box .round-social{margin-top:100px}.contact2 .round-social a{background:transparent;margin:0 7px;padding:11px 12px}.contact2 .contact-container .links a{color:#8d97ad}.contact2 .contact-container{position:relative;top:200px}.contact2 .btn-danger-gradiant{background:#ff4d7e;background:linear-gradient(to right, #ff4d7e 0%, #ff6a5b 100%)}.contact2 .btn-danger-gradiant:hover{background:#ff6a5b;background:linear-gradient(to right, #ff6a5b 0%, #ff4d7e 100%)}.bg1{background-image:url("https://www.wrappixel.com/demos/ui-kit/wrapkit/assets/images/contact/map.jpg")}.bg2{background-image:url("https://www.wrappixel.com/demos/ui-kit/wrapkit/assets/images/contact/1.jpg")}

.container{display:-webkit-flex;display:flex;margin:15px;-webkit-justify-content:space-around;justify-content:space-around;text-align:center;-webkit-flex-direction:row;flex-direction:row}.contain{display:column;margin:5px;text-align:center}.image{margin-top:100px;background-repeat:no-repeat;width:400px;height:400px;border-radius:50%;background-position:center;background-size:cover,center}.heading{color:blue;letter-spacing:0.5mm;font-size:30px}@media screen and (min-width: 1000px) and (max-width: 1150px){.image{height:350px;width:350px}}@media screen and (min-width: 800px) and (max-width: 1000px){.image{height:300px;width:300px}}@media screen and (max-width: 800px) and (min-width: 500px){.container{-webkit-flex-direction:column;flex-direction:column}.image{display:block;margin-left:auto;margin-right:auto}}@media screen and (max-width: 500px) and (min-width: 250px){.container{-webkit-flex-direction:column;flex-direction:column}.image{height:200px;width:200px;display:block;margin-left:auto;margin-right:auto}}@media screen and (max-width: 250px) and (min-width: 50px){.container{-webkit-flex-direction:column;flex-direction:column;margin:0}.contain{margin:0}.image{height:150px;width:150px;display:block;margin-left:auto;margin-right:auto}}

